import React, { useState, useEffect } from "react"
import styles from './Search.module.scss';
import SearchResults from "./searchResults"

const Search = () => {

    const [search, setSearch] = useState([]);
    const [posts, setPosts] = useState([]);
    const [status, setStatus] = useState(false);


    const doSearch = (search) => {
        setSearch(search);
        setStatus(true)
    }


    let res = []
    if (search.length > 2 && status == true && window.__LUNR__) {
        window.__LUNR__.__loaded.then((lunr) => {
            const refs = lunr.en.index.search(search);
            res = refs.map(({ ref }) => lunr.en.store[ref]);
            setPosts(res)
            setStatus(false)
        });

    }

    return (
        <>
            You can user this search page to find what you're looking for
            <form>
                <label className={styles['search__label']} htmlFor="search-input">
                    Search:
                </label>

                <input className={styles['search__input']}
                    type="search"
                    id="search-input"
                    name="keywords"
                    aria-controls="search-results-count"
                    onChange={(e) => { doSearch(e.target.value) }}
                />
            </form>
            {
                !!posts.length && (
                    <SearchResults query={search} results={posts} />
                )
            }

        </>
    )
}

export default Search;