import React from "react"
import Highlighter from "react-highlight-words"

const SearchResults = ({ query, results }) => {

    return (
        <section aria-label="Search results for all posts">

            {
                !!results.length && query && (
                    <h2
                        className="search-results-count"
                        id="search-results-count"
                        aria-live="assertive"
                    >
                        Found {results.length} posts on "{query}"
      </h2>
                )
            }
            {
                !!results.length && (
                    <ol className="search-results-list">
                        {results.map(({ title, date, description, tags, slug }) => (
                            <li key={title}>
                                <h3 className="search-results-list__heading">
                                    <a href={slug} className="search-results-list__link">
                                        <Highlighter
                                            searchWords={[query]}
                                            autoEscape={true}
                                            textToHighlight={title}
                                        />
                                    </a>
                                </h3>
                                <small>{new Date(date).toLocaleString("en-GB")}</small>
                                {description && (
                                    <p>
                                        <Highlighter
                                            searchWords={[query]}
                                            autoEscape={true}
                                            textToHighlight={description}
                                        />
                                    </p>

                                )}
                                {tags && (
                                    <p>
                                        <strong>Tags:</strong> <Highlighter
                                            searchWords={[query]}
                                            autoEscape={true}
                                            textToHighlight={tags.join(', ')}
                                        />
                                    </p>

                                )}
                            </li>
                        ))}
                    </ol>
                )
            }
        </section >
    )


}

export default SearchResults